body{
    /* background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%); */
    background: rgb(2,0,36);
    background: linear-gradient(90deg, #051f30, #000000);
    display: flexbox;
    width: 100%;
}
#loginform {
    background:rgba(250,250,250,90%);
    min-width: 100px;
    max-width: 600px;
    /* height: 450px; */
    margin: auto;
    margin-top: 150px;
    border: 1px solid #dddfe2;
    padding: 20px;
    padding-top: 30px;
    border-radius: 10px;
    box-shadow: 1px -1px 48px -5px rgba(0, 0, 0, 0.4);
    /* animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; */
    animation: slide-left 1.2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;

}
#loginform h1{
    text-align: center;
}

#loginform input {
    background:rgba(250,250,250,90%);
    width: 94%;
    padding: 15px;
    height: 20px;
    margin-top: 10px;
    border: 1px solid rgb(221, 223, 226);
    border-radius: 10px;
    font-size: 15px;

}

#loginform .forgot {
    color: blue;
    margin-top: 20px;
    text-align: center;
}

#loginform .forgot:active {
    transform: scale(0.96);
}

#loginform .forgot:hover {
    text-decoration: underline;
}

#loginform .form-group {
    margin-bottom: 10px;
    color: white;
    font-size: x-large;
    text-align: left;


}

#loginform button {
    border: 2px solid blue;
    position: relative;
    display: flexbox;
    border-radius: 10px;
    /* max-width:100px; */
   width: 100%;
   /* margin-left: 190px; */
    /* min-width: 100px; */
    height: 40px;
    font-size: larger;
    margin-top: 20px;
    background-color: blue;
    color: white;
    font-weight: bold;

}

#loginform button:active {
    transform: scale(0.96);
}


#loginform .button {
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

#loginform .button a {
    position: relative;
    width: 200px;
    height: 45px;
    background: rgb(1, 170, 1);
    margin: 20px;
    text-align: center;
    font-size: larger;
    color: white;
    text-decoration: none;
    border-radius: 10px;
    padding-top: 20px;
}

#loginform .button a:active {
    transform: scale(0.96);
}



@keyframes scale-up-center {
    0% {
        transform: scale(100%);
    }

    100% {
        transform: scale(0);
    }
}