
#signupform {
    background:rgba(250,250,250,90%);
    max-width: 600px;
   min-width: 100px;
    /* height: 450px; */
    margin: auto;
    margin-top: 150px;
    border: 1px solid #dddfe2;
    padding: 20px;
    padding-top: 30px;
    border-radius: 10px;
    box-shadow: 1px -1px 48px -5px rgba(0, 0, 0, 0.4);
    /* animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; */
    animation: slide-left 1.2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
    display:flexbox;
    backdrop-filter: blur(50%);
}
#signupform h1{
    text-align: center;
}
#signupform input {
    background:rgba(250,250,250,90%);
    width: 94%;
    padding: 15px;
    height: 20px;
    margin-top: 10px;
    border: 1px solid rgb(221, 223, 226);
    border-radius: 10px;
    font-size: 15px;

}



#signupform .form-group {
    margin-bottom: 10px;
    color: white;
    font-size: x-large;
    text-align: left;


}

#signupform button {
    border: 2px solid blue;
    border-radius: 10px;
    width: 594px;
    height: 40px;
    font-size: larger;
    margin-top: 20px;
    background-color: blue;
    color: white;
    font-weight: bold;

}

#signupform button:active {
    transform: scale(0.96);
}


#signupform .button {
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

#signupform .button a {
    position: relative;
    width: 200px;
    height: 45px;
    background: rgb(1, 170, 1);
    margin: 20px;
    text-align: center;
    font-size: larger;
    color: white;
    text-decoration: none;
    border-radius: 10px;
    padding-top: 20px;
}

#signupform .button a:active {
    transform: scale(0.96);
}
#signupform li {
    display: inline-block;
}
#signupform a{
    text-decoration: none;
    padding: 5px;
}
#signupform p {
    text-align: center;
    font-size: large;
}


@keyframes scale-up-center {
    0% {
        transform: scale(0.5);
    }

    100% {
        transform: scale(1);
    }
}