#calculator {
    background-color: #ffffff;
    margin: auto;
    max-width: 325px;
    border: 10px solid  rgb(61, 61, 61);
    padding: 20px;
    margin-top: 150px;
    height: 500px;
    border-radius: 20px;
    box-shadow: 10px 10px 50px 26px rgb(0, 0, 0);
    /* animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; */
    animation: slide-left 1.2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both; 
}

#calculator input {
    width: 96%;
    padding: 3px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 10px;
    border: 5px solid gray;
    font-size: x-large;
    background-color: rgb(64, 107, 71);
}

#calculator .number1 .div{
    background-color: orange;

}

#calculator button {
    width: 24.0%;
    background-color: black;
    color: #ffffff;
    padding: 20px;
    margin-right: 3px;
    margin-bottom: 10px;
    margin-top: 10px;
    border-radius: 10px;
    font-size: larger;
    border:  2px solid white;
    transition: all 0.2s ease;
}

#calculator button:active {
    transform: scale(0.96);
}

#calculator .number2 .add {
    background-color: orange;
}

#calculator .number3 .mul  {
    background-color: orange;
}

#calculator .number4 .C  { 
    background-color: rgb(0, 0, 0);
}
#calculator .number4 .sub { 
    background-color: orange;
}

#calculator .ac .AC  {
    width: 50%;
    background-color: red;
}
#calculator .eql{
    background-color: green;
    width: 48%;
}

@keyframes scale-up-center {
    0% {
        transform: scale(100%);
    }

    100% {
        transform: scale(0);
    }
}