#home{
  /* display: inline-flex; */
  /* max-width: 500px; */
  min-width: 200px;
}
.name{
    color: white;
    /* display: inline-block; */
    margin-top: 200px;
    margin-left: 100px;
    animation: slide-left 1s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
    /* animation: fade-out-left 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both; */
  }
 .contact {
    color: white;
    /* display: inline-block; */
    margin-left: 100px;
    animation: slide-left 1.4s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;

}
.btn{
  /* display: inline-block; */
    margin-top: 100px;
    margin-left: 100px;
    animation: slide-left 1.6s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}
.btn button{
  /* display: inline-block; */
  background-color:aqua;
  border-color:aqua;
  border-radius: 20px;
  color:black;
  padding:20px 15px 20px 15px;

}
 button:active {
  transform: scale(0.96);
}
.btn button:hover{
  box-shadow: 0px 0px 60px 3px rgba(150,248,255,1);
}
 .contact i,span{
    animation: slide-left 1s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
    
}
.name p{
    width: 50%;
    padding-top: 5px;
    animation: slide-left 1.2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;

 }
 .image img{
  display: inline-block;
  /* max-width: ; */
  /* max-width: 500px; */
  height: 600px;
  margin-left: 1102px;
  width: fit-content;
  margin-top:-600px;
  background-color: white;
  border-radius: 40% 0 0 40%;
  animation: slide-left 1.8s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;

 }
 @keyframes slide-left {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(0px);
    }
  }
