.card {
    border: 5px solid   rgb(112, 112, 112);
    background-color: white;
    color: black;
    margin: 10px;
    margin-left: 20px;
    padding: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 10px;
}
#empdetails .card{
    margin-left: 200px;
    width: fit-content;
}
button {
    background-color: blue;
    color: white;
    border: 1px solid blue;
    border-radius: 20px;
    padding: 1px 10px 1px 10px;
}
button a:active {
    transform: scale(0.96);
}
.card img {
    width: 180px;

}