#pagination table,  td,
 th {
  border: 1px solid #ddd;
  padding: 12px;
  max-width: 100%;
  min-width: 0%;
  font-family: sans-serif;
  border-collapse: collapse;
  border: 1px solid rgb(0, 0, 0);
  padding: 10px 20px;
  font-size: medium;
}
#pagination h1{
    color: white;
}

#pagination th {
  background-color: #00fffc;
  color: rgb(0, 0, 0);
  text-transform: uppercase;
  border-collapse: collapse;
  font-weight: bold;
  padding: 12px;
  text-align: center;
}

#pagination td:hover {
  background-color: #575454;
}
.controls button {
  border-radius: 5px;
  margin: 2px;
  border: 1px solid black;
  padding: 10px;
  background-color: white;
  color: black;
  transition: all 0.2s ease;
}
.controls button:active{
  transform: scaleX(0.96);
}
.controls button.currentpage{
  background-color:#00fffc;
}