#empdetails {
  display: flex;
  align-items: center;
  padding-left: 10px; 
  max-width: fit-content;
  /* min-width: 100px;  */
}
#empdetails table {
  width: 600px ;
  min-width: 100px;
  padding: 5px 0px 5px 0px;
  margin-left: 50px;
  margin-top: 125px;
  margin-bottom: 100px;
  padding-bottom: 10px;
  border-collapse: collapse;
  text-align: center;
  border: 1px solid rgb(0, 0, 0);
  color: black;
  background-color: white;
}
  
#empdetails table,
tr,
td {
  border: 1px solid black;
  background-color: white;
  padding: 10px;
  text-align: center;
  
}
#empdetails table th {
  border: 1px solid black;
  background-color: #00fffc;
  color: white;
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
}
