nav li {
    position: relative;
    display: inline-block;
    text-decoration: none;
    color: #fff;
    font-weight: 700;
}

/* nav li:hover{ */
    /* background-color: white; */
    
/* } */
nav li::before {
    top: -3px;
    transform-origin: right;
}

nav li::after {
    bottom: -3px;
    transform-origin: left;
}

nav li:hover::before,
nav li:hover::after,
nav .active::before,
nav .active::after {
    transform: scaleX(1);
}

nav li::before,
nav li::after {     
    content: '';
    left: 0;
    right: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
    transform: scaleX(0);
    transition: transform 0.7s;
}

nav a:hover {
    color:#00fffc;
    font-size: x-large;
}

li span:nth-child(2) {
    display: none;
}

nav:hover {
    height: 5%;


}

nav:hover li span:nth-child(2) {
    display: block;
}

ul {
    margin-top: 0px;
}